.login-container {
    height: 100%;
    display: flex;
    /* Alinha o item horizontalmente */
    justify-content: center;
    /* Alinha o item verticalmente */
    align-items: center;
}

.login-container form {
    width: 100%;
    max-width: 300px;
    display: flex;
    flex-direction: column;
}

.login-container form input {
    margin-top: 20px;
    border: 1px solid #ddd;
    border-radius: 4px;
    height: 48px;
    padding: 0 20px;
    font-size: 16px;
    color: #707070;
}

.login-container form input::placeholder {
    color: #a0a0a0;
}

.login-container form button {
    margin-top: 10px;
    border: 0;
    border-radius: 4px;
    height: 48px;
    font-size: 16px;
    background: #DF4723;
    font-weight: bold;
    color: #fff;
    cursor: pointer;
}